.services {
  background-color: #1b2d37;
}

.services-wrapper {
  width: 1170px;
  margin: 0 auto;
  padding-top: 88px;
  padding-bottom: 127px;
}

.services-title {
  margin: 0;
  margin-bottom: 52px;
  padding: 0;
  font-family: $font-roboto-condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;
  color: $white;
}

.services__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.services__item {
  margin-top: 30px;
  margin-right: 30px;
}

.services__item:nth-child(3n) {
  margin-right: 0;
}

.services__item:nth-child(-n+3) {
  margin-top: 0;
}

.services__item-link {
  text-decoration: none;
  display: block;
  padding: 50px 45px;
  width: 370px;
  min-height: 620px;
  position: relative;
  box-sizing: border-box;
  cursor: pointer;
  background-color: transparent;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0.2;
  }

  &:hover::before,
  &:focus::before {
    opacity: 0.5;
  }

  &:active::before {
    opacity: 0.8;
  }
}

.services__item-link-1::before {
  background-image: url("../img/services-image1.png");
}

.services__item-link-2::before {
  background-image: url("../img/services-image2.png");
}

.services__item-link-3::before {
  background-image: url("../img/services-image3.png");
}

.services__item-button {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 63px;
  height: 63px;
  background-color: $blue;
  display: flex;
  justify-content: center;
  align-items: center;
}

.services__item-title {
  position: absolute;
  top: 50px;
  left: 45px;
  right: 45px;
  margin: 0;
  padding: 0;
  font-family: $font-roboto-condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 30px;
  color: $white;
}

.services__item-arrow {
  border-right: 1px solid white;
  border-bottom: 1px solid white;
  width: 5px;
  height: 5px;
  transform: rotate(-45deg);
}
