.about {
  background-image: url("../img/about-rectangle1.svg"),
                    url("../img/about-rectangle2.svg"),
                    url("../img/about-rectangle3.svg"),
                    url("../img/about-rectangle4.svg"),;
  background-position: top 46px left calc(50% - 590px),
                       top 444px left calc(50% - 670px),
                       top 147px left calc(50% + 36px),
                       top 396px left calc(50% + 765px),;
  background-color: transparent;
  background-size: auto;
  background-repeat: no-repeat;
}

.about__wrapper {
  width: 1170px;
  margin: 0 auto;
  padding-top: 112px;
  padding-bottom: 111px;
}

.about__wrapper-company {
  padding-top: 69px;
  padding-left: 182px;
}

.about__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.about__item {
  margin-bottom: 30px;
}

.about__item:nth-last-of-type(-n+2) {
  margin-bottom: 0;
}

.about__link {
  display: block;
  padding: 94px 43px 50px 42px;
  background-color: $white;
  box-sizing: border-box;
  position: relative;
  border: 1px solid #ebebeb;
  border-left: 3px solid $blue;
  text-decoration: none;
  width: 570px;
  min-height: 299px;
}

.about__item-icon {
  position: absolute;
  top: 40px;
  left: 45px;
  fill: $blue;
}

.about__item-title {
  font-family: $font-roboto-condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 34px;
  color: $blue;
  margin: 0;
  margin-bottom: 16px;
  padding: 0;
}

.about__item-text {
  font-family: $font-ptmono;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #666666;
  margin: 0;
  padding: 0;
}

.about__link:hover,
.about__link:focus {
  cursor: pointer;
  border: 1px solid $blue;
  background-color: $blue;
}

.about__link:hover > .about__item-icon,
.about__link:focus > .about__item-icon {
  fill: $white;
}

.about__link:hover > .about__item-title,
.about__link:focus > .about__item-title {
  color: $white;
}

.about__link:hover > .about__item-text,
.about__link:focus > .about__item-text {
  color: $white;
}

.about__link:active {
  border: 1px solid rgba(1, 178, 255, 0.7);
  background-color: rgba(1, 178, 255, 0.7);
}

.about__title {
  position: relative;
  margin-bottom: 21px;
  padding: 0;
  font-family: $font-roboto-condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;
  color: #666666;
}

.about__title::before {
  content: "2005";
  position: absolute;
  top: 54px;
  left: -278px;
  background-image: url("../img/about-background.png");
  background-size: 305px 115px;
  background-repeat: no-repeat;
  background-color: #00b35d;
  background-position: 5px 51px;
  font-family: $font-roboto-condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 156.248px;
  line-height: 220px;
  color: transparent;
  transform: rotate(-90deg);
  background-clip: text;
}

.about__text {
  margin: 0;
  margin-bottom: 21px;
  padding: 0;
  font-family: $font-ptmono;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #666666;
}

.about__detail {
  margin-top: 17px;
  width: 180px;
  font-family: $font-roboto-condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $blue;

  &:hover,
  &:focus {
    color: $white;
    background-color: $blue;
  }

  &:focus {
    color: rgba(255, 255, 255, 0.7);
    background-color: rgba(1, 178, 255, 0.7);
  }
}

.about__year {
  position: absolute;

  font-family: $font-roboto-condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 156.248px;
  line-height: 220px;
  color: #00b35d;
  transform: rotate(-90deg);
}
